.bmix-list-icon{
    &-content{
        ul{
            li{
                font-weight: 700;
                font-size: 24px;
                line-height: 32px;
                position: relative;
                letter-spacing: -0.005em;
                padding-left: 35px;
                margin-bottom: 15px;
                color: var(--heading-color);
                &:last-child{
                    margin-bottom: 0;
                }
                img{
                    position: absolute;
                    top: 5px;
                    left: 0;
                }
            }
        }
        &.pricing-v5{
            ul {
                li{
                    font-size: 20px;
                    line-height: 28px;
                    letter-spacing: -0.001em;
                    img{
                        position: absolute;
                        top: 2px;
                        left: 0;
                    }
                }
            }
        }
    }
}
.bmix-list-icon{
    &-wrap{
        display: flex;
        gap: 80px;
        @include max (767px){
            display: block;
            &:last-child{
                margin-top: 30px;
            }
        }
    }
    &-title{
        ul{
            li{
                position: relative;
                padding-left: 40px;
                img{
                    position: absolute;
                    top: 2px;
                    left: 0;
                    width: 23px;
                }
                h5{
                    margin-bottom: 10px;
                }
            }
        }
        @include max (767px){
            &:last-child{
                margin-top: 30px;
            }
        }
    }
}