.bmix-breadcrumb{
  text-align: center;
  margin-top: 134px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 150px 0;
  position: relative;
  &::before{
    content: "";
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;
    opacity: .9;
    mix-blend-mode: multiply;
    background: var(--heading-color);
  }
  @include max(991px) {
    margin-top: 84px;
    padding: 120px 0 80px;
  }
  @include max(767px) {
    padding: 100px 0 80px;
  }
  @include max(575px) {
    padding: 80px 0 60px;
  }
}

h1.post__title{
  position: relative;
  margin-bottom: 30px;
  color: white;
}

.single-blog{
  h1.post__title{
      font-size: 70px!important;
      line-height: 80px;
  }
}

.breadcrumbs{
  ul{
    li{
      font-size: 21px;
      font-weight: 700;
      line-height: 1;
      z-index: 0;
      padding: 0 5px;
      position: relative;
      display: inline-block;
      color: var(--white-color);
      @include max(767px) {
        font-size: 18px;
      }
      &:first-child{
        padding-left: 0;
      }
      &:last-child{
        padding-right: 0;
      }
      &:before {
        content: "";
        right: -8px;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        position: absolute;
        background-size: 9px;
        background-position: center right;
        background-repeat: no-repeat;
        background-image: url(../images/icon/arrow-right7.svg);
      }
      &:last-child:before{
        content: none;
      }
      a{
        font-weight: 400;
        color: #FDFDE1;
      }
    }
  }
}
