
.bmix-logo-icon-item{
    padding: 0 20px;
}
.bix-brand-logo-title{
    padding-bottom: 60px;
    @include max (991px){
        padding-bottom: 40px;
    }
    @include max (767px){
        padding-bottom: 30px;
    }
    p{  text-align: center;
        color: var(--heading-color);
    }
}

.bmix-infinite-slider{
    &-data{
        h2{
            color: var(--white-color);
        }
    }
    .slick-list{
        height: 60px;
        @include max(1199px) {
            height: 50px;
        }
    }
    .slick-slide {
        width: auto!important;
    }
}
