.bmix-p{
  &-wrap{
    position: relative;
    &:hover{
      .bmix-p-content{
        opacity: 1;
        ::before{
          opacity: 1;
        }
      }
      
      .bmix-p-thumb img{
        transform: scale(1.1);
      }
    }
    .bmix-p-content {
      position: absolute;
      transform: translate(-50%, -50%);
      top: 50%;
      left: 50%;
      opacity: 0;
      z-index: 1;
      transition: all .4s;
      text-align: center;
      h4{
        color: var(--white-color);
        margin-bottom: 24px;
      }
    }
  }
  &-thumb{
    position: relative;
    display: block;
    margin-bottom: 24px;
    text-align: center;
    overflow: hidden;
    border-radius: 10px;
    img{
      border-radius: 10px;
      object-fit: cover;
      transition: all .4s;
    }
    &::before{
      position: absolute;
      content: "";
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      z-index: 1;
      opacity: 0;
      transition: .4s;
      background: linear-gradient(225deg, #1263FF 0%, #27AFF9 100%);
    }
    &:hover::before {
      opacity: .9;
    }
  }
  // start v6
  
  &-wrap2{
    margin-bottom: 24px;
    position: relative;
    display: block;
    &:hover{
      .bmix-p-content-wrap{
        opacity: 1;
        ::before{
          opacity: 1;
        }
      }
      .bmix-p-thumb2{
        img{
          transform: scale(1.1);
        }
      }
    }
  }
  &-thumb2{
    text-align: center;
    display: block;
    position: relative;
    overflow: hidden;
    border-radius: 10px;
    img{
      border-radius: 10px;
      object-fit: cover;
      transition: all .4s;
    }
    &::before{
      position: absolute;
      content: "";
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      opacity: 0;
      z-index: 1;
      transition: .4s;
      background: var(--heading-color);
    }
    &:hover::before {
      opacity: .7;
    }
  }
  &-content{
    &-wrap {
      position: absolute;
      bottom: 30px;
      left: 30px;
      opacity: 0;
      z-index: 1;
      transition: all .4s;
    }
    &-data{
      text-align: start;
      h5{
        margin-bottom: 5px;
        color: var(--white-color);
      }
      p{
        font-size: 18px;
        line-height: 26px;
        letter-spacing: -0.001em;
        color: var(--light-color2);
      }
    }
    &-icon {
      width: 50px;
      height: 50px;
      background: #1263FF;
      border-radius: 100px;
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      bottom: 0;
      right: -100px;
    }
  }
  // strat v7
  &-wrapv7{
    padding: 27px;
  }
  &-iconv7{
    margin-bottom: 30px;
  }
  &-contentv7{
    h4{
      margin-bottom: 15px;
    }
  }
  // strat v8
  &-wrapv8{
    position: relative;
    transition: all 0.4s;
    display: block;
    &:hover{
      // .bmix-p-thumbv8{
      //   transform: scale(1.1);
      // }
      .bmix-p-contentv8{
        transform: translateY(-60px);
        background-color: var(--white-bg);
        box-shadow: 0px 4px 80px 0px #00000014;
        a.bmix-icon-btn{
          opacity: 1;
        }
      }
    }
  }
  &-thumbv8{
    position: relative;
    overflow: hidden;
    transition: all .4s;
    img{
      width: 100%;
      object-fit: cover;
      transition: all .4s;
    }
  }
  &-contentv8{
    border-radius: 5px;
    padding: 24px 30px;
    width: calc(100% - 60px);
    margin-left: 30px;
    transition: all 0.4s;
    h5{
      margin-bottom: 5px;
    }
    p{
      font-size: 18px;
      line-height: 26px;
      margin: 0 0 15px;
    }
    a.bmix-icon-btn{
      opacity: 0;
      font-weight: 700;
      font-size: 16px;
      line-height: 22px;
      img{
        height: 14px;
        padding-left: 7px;
        width: auto;
      }
    }
  }
  // v9
  &-wrapv9{
    padding: 50px;
    border-radius: 10px;
    position: relative;
    background: var(--heading-color);
    border: 1px solid var(--heading-color);
    .bmix-process-number{
      position: absolute;
        top: 0;
        left: 0;
      h5{
        width: 55px;
        height: 55px;
        display: flex;
        transition: all .8s;
        align-items: center;
        border-radius: 100px;
        margin-bottom: 24px;
        justify-content: center;
        font-size: 24px;
        line-height: 32px;
        transition: all 0.4s;
        letter-spacing: -0.005em;
        color: var(--accent-color)!important;
        border: 1px solid var(--white-bg);
        background-color: var(--white-bg);
      }
    }
  }
  // portfolio details
  &-d{
    &-thumb{
      padding-bottom: 30px;
    }
    &-wraper{
      &.mr-80{
        margin-right: 80px;
        @include max (991px){
          margin-right: 0;
        }
      }
    }
    &-content{
      h3{
        margin-bottom: 10px;
      }
      ul{
        li{
          font-weight: 700;
          margin-bottom: 10px;
          span{
            font-size: 21px;
            font-weight: 400;
            line-height: 30px;
            letter-spacing: -0.01em;
          }
        }
      }
    }
    &-wrap{
      padding: 40px 20px;
      border-radius: 10px;
      box-shadow: 0px 4px 80px 0px #00000014;
    }
    &-date{
      margin-bottom: 24px;
      p{
        margin-bottom: 5px;
      }
    }
    &-website{
      ul{
        li{
          font-size: 24px;
          font-weight: 700;
          line-height: 32px;
          letter-spacing: -0.005em;
          color: var(--accent-color);
        }
      }
    }
  }
}
