// bmix cta v1 start

.bmix-cta-subscription{
  &-field{
    position: relative!important;
    input{
      width: 66%;
      font-size: 16px;
      line-height: 22px;
      background: transparent;
      color: var(--light-color2);
      padding: 15px 20% 15px 24px;
      @include max (991px){
        width: 60%;
        padding: 15px 22% 15px 24px;
      }
      @include max (767px){
        width: 100%;
        padding: 15px 24px 15px 24px;
      }
      &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
        color: var(--light-color2);
      }
      &::-moz-placeholder { /* Firefox 19+ */
        color: var(--light-color2);
      }
      &:-ms-input-placeholder { /* IE 10+ */
        color: var(--light-color2);
      }
      &:-moz-placeholder { /* Firefox 18- */
        color: var(--light-color2);
      }
    }
  }
}

// bmix v2
.content-section{
  h2,p{
    color: var(--white-color);
  }
}
.bmix-form-content{
  &-box {
    padding: 30px;
    border-radius: 10px;
    background: var(--light-color2);
    @include max (991px){
      margin-top: 40px;
    }
    .form-field-item{
      margin-bottom: 24px;
    }
    ul.nav {
      background: var(--accent-color);
      border-radius: 50px;
      height: 60px;
      transition: all 0.4s;
      &:hover{
        background: var(--heading-color);
      }
      li{
        a{
          color: var(--white-bg) !important;
          font-weight: 700;
          font-size: 16px;
          line-height: 24px;
          padding: 18px 42px;
        }
      }
  }
}
  &-title{
    margin-bottom: 30px;
    h4{
      margin-bottom: 12px;
    }
    p{
      font-size: 18px;
      line-height: 26px;
      color: var(--white-smoke);
      letter-spacing: -0.001em;
    }
  }
}