.bmix-popup-video{
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  color: var(--white-color);
  &:hover{
    color: var(--white-color);
  }
  img{
    margin-right: 15px;
  }
}
// End hero

// star v3
.bmix-video-thumb{
  text-align: center;
  position: relative;
  img{
    border-radius: 10px;
  }
  @include max (991px){
    margin-bottom: 30px;
  }
}
.bmix-popup-video2{
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  img{
    @include max(767px) {
      width: 80px;
      height: 80px;
    }
  }
}


.waves {
  position: absolute;
  width: 210px;
  height: 210px;
  background: var(--white-bg);
  opacity: 0;
  border-radius: 100%;
  left: -65px;
  top: -65px;
  z-index: 0;
  -webkit-animation: waves 3s ease-in-out infinite;
  animation: waves 3s ease-in-out infinite;
  z-index: -1;
  @include max(991px) {
    width: 160px;
    height: 160px;
    left: -40px;
    top: -40px;
  }
  &.waves2{
    left: -55px;
    top: -55px;
    background-color: var(--accent-color);
    @include max(991px) {
      left: -40px;
      top: -40px;
      width: 180px;
      height: 180px;
    }
    @include max(767px) {
      left: -52px;
      top: -52px;
    }
  }
  &.waves3{
    left: -55px;
    top: -55px;
    background-color: var(--dark-bg);
    @include max(991px) {
      left: -40px;
      top: -40px;
      width: 180px;
      height: 180px;
    }
    @include max(767px) {
      left: -52px;
      top: -52px;
    }
  }
  &.wave-1 {
    -webkit-animation-delay: 0s;
    animation-delay: 0s;
  }
  &.wave-2 {
    -webkit-animation-delay: 1s;
    animation-delay: 1s;
  }
  &.wave-3 {
    -webkit-animation-delay: 2s;
    animation-delay: 2s;
  }

 
}

@keyframes waves {
  0% {
      -webkit-transform: scale(0.2, 0.2);
      transform: scale(0.2, 0.2);
      opacity: 0;
  }

  50% {
      opacity: 0.9;
  }

  100% {
      -webkit-transform: scale(0.9, 0.9);
      transform: scale(0.9, 0.9);
      opacity: 0;
  }
}

// End v3
