// fonts variable
$body-font: 'Open Sans', sans-serif!default;
$heading-font: 'Montserrat', sans-serif !default;

// color variable
:root {
  --white-bg: #fff;
  --dark-bg: #060E28;
  --grey-color: #323F5B;
  --heading-color: #060E28;
  --body-color: #1E263D;
  --accent-color: #1263FF;
  --accent-color2: #27AFF9;
  --white-smoke: #2D374A;
  --white-smoke2: #FDFDFD;
  --white-color: #fff;
  --light-color: #060E28;
  --light-color2: #F4F4F4;
  --light-color3: #F6F6F6;
  --light-color4: #DDDFE2;
}
$colors: (
 
);

$bodyfontScale: (
  xl: (
    20: 20px,
    lineheight: 32px,
  ),
  lg: (
    18: 18px,
    lineheight: 28px,
  ),
  md: (
    16: 16px,
    lineheight: 24px,
  ),
  xs: (
    14: 14px,
    lineheight: 22px,
  ),
  xss: (
    12: 12px,
    lineheight: 20px,
  ),
);

// button font size
$button_font_size: 16px;
$buton_line_height: 22px;
$button_padding: 19px 36px;
$button_font_weight: 700;
$button_font_family: $body-font;

