.bmix-hero{
  &-section {
    z-index: 0;
    position: relative;
    padding: 250px 0 130px;
    background-size: cover;
    background-position: center;
    @include max (1199px){
      padding: 200px 0 130px;
    }
    @include max (991px){
      padding: 180px 0 100px;
    }
    @include max(767px) {
      padding: 150px 0 80px;
    }
    &::before{
      content: "";
      left: 0;
      top: 0;
      z-index: -1;
      opacity: 0.9;
      width: 100%;
      height: 100%;
      position: absolute;
      background: linear-gradient(95.27deg, #1263FF 24.85%, #27AFF9 75.6%);
    }
  }
  &-content{
    &.medium-mw{
      max-width: 760px;
    }
    &.center{
      text-align: center;
      max-width: 1150px;
      margin: 0 auto;
    }
    &.center2{
      text-align: center;
      max-width: 1220px;
      margin: 0 auto;
    }
    &.mw-900{
      max-width: 880px;
    }
    p{
      color: var(--white-color)!important;
    }
    // content hero v3
    &.bmix-hero-contentv3{
      position: relative;
      margin-left: 35%;
      padding: 70px 80px 70px 200px;
      @include max (1800px){
        margin-left: 31%;
      } 
      @include max (1499px){
        margin-left: 15%;
        padding: 38px 26px 28px 200px;
      }
      @include max (1199px){
        margin-left: 15%;
        padding: 0 44px 0 100px;
      }
      @include max (991px){
        padding: 0 44px 0 50px;
        margin-left: 13%;
      }
      @include max (767px){
        padding: 0 8px;
        margin-left: 0;
      }
      &::before{
        content: "";
        left: 0;
        top: 0;
        z-index: -1;
        width: 100%;
        height: 100%;
        transition: all .4s;
        position: absolute;
        background-image: url(../images/v3/bg1.png);
        @include max (1199px){
          background-image: none;
        }
      }
      // background: var(--dark-bg);
      // padding: 100px 80px 100px 208px;
    }
    // &.bmix_screenfix_left{
    //   margin-left: calc((-33vw + 100%) / 2);
    //   max-width: 1270px;
    // }
    // end v3
    &.large-mw{
      max-width: 960px;
    }
     // end v5
    &.mw-extra-lg{
    padding: 70px;
    margin-left: 21%;
    max-width: 1000px;
    border-radius: 10px;
    background: var(--dark-bg);
    @include max (1199px){
      margin-left: 15%;
    }
    @include max (991px){
      margin-left: 0;
    }
    @include max (767px){
      padding: 50px;
    }
    @include max (575px){
      padding: 0;
      background: none;
    }
  }
}

