.bmix-hero-btn{
  &-wrap{
    gap: 24px;
    display: flex;
    flex-wrap: wrap;
    position: relative;
    align-items: center;
  }
  &-wrap2{
    gap: 27px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
}

.bmix-hero-author{
  &-wrap{
    gap: 24px;
    display: flex;
    max-width: 470px;
    align-items: center;
    @include max (991px){
      text-align: left;
    }
    @include max (767px){
      display: block;
      max-width: 330px;
    }
  }
  &-thumb{
    flex-shrink: 0;
  }
  &-data{
    @include max (767px){
      margin-top: 15px;
    }
    h6{
      font-size: 18px;
      line-height: 26px;
      color: var(--white-color);
      letter-spacing: -0.001em;
      font-family: $body-font;
    }
  }
  // end v1
  &-contentv4{
    img{
      margin-bottom: 20px;
    }
    p{
      color: var(--light-color3)!important;
    }
  }
}
