h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-family: $heading-font;
  color: var(--heading-color);
  font-weight: 700;
  margin: 0;
}
h1,
.h1 {
  font-size: 100px;
  line-height: 1em;
  color: #fff;
  letter-spacing: -0.005em;
  margin-bottom: 24px;
  @include max(1399px) { 
    font-size: 90px;
  }
  @include max(1199px) { 
    font-size: 80px;
  }
  @include max(991px) { 
    font-size: 60px;
  }
  @include max(767px) { 
    font-size: 48px;
  }
}
h2,
.h2 {
  font-size: 65px;
  line-height: 1.07em; /* 116.667% */
  letter-spacing: -0.005em;
  @include max(1199px) { 
    font-size: 48px;
  }
  @include max(767px) { 
    font-size: 42px;
  }
}

h3,
.h3 {
  font-size: 30px;
  line-height: 40px;
  letter-spacing: -0.005em;
  @include max(767px) {
    line-height: 1.2em;
  }
  @include max(575px) {
    font-size: 30px;
  }
}
h4,
.h4 {
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.005em;
}
h5,
.h5 {
  font-size: 20px;
  line-height: 28px;
  letter-spacing: -0.001em;
}

p{
  &:last-child{
    margin-bottom: 0;
  }
}


@for $i from 10 through 40 {
  .f-size-#{$i} {
    font-size: $i * 1px !important;
  }
}

@for $i from 10 through 40 {
  .line-height-#{$i} {
    line-height: $i * 1px !important;
  }
}

.font-bold{
  font-weight: 700;
}
.font-black{
  font-weight: 800;
}
.font-medium{
  font-weight: 500;
}
.font-normal{
  font-weight: 400;
}
.font-semibold{
  font-weight: 600;
}


