
.bmix-iconbox{
    &-wrap{
        display: flex;
        padding: 30px;
        align-items: center;
        border-radius: 10px;
        transition: all 0.4s;
        margin-bottom: 24px;
        justify-content: space-between;
        border: 1px solid var(--light-color2);
        @include max (575px){
            align-items: start;
        }
        &:hover{
            box-shadow: 0px 4px 80px 0px #00000014;
            background: var(--white-bg);
            svg path{
                fill: var(--accent-color);
            }
        }
    }
    &-content{
        display: flex;
        gap: 15px;
    }
    &-data{
        position: relative;
        h5{
            margin-top: 20px;
        }
    }
   &-title{
    margin-bottom: 24px;
    .border-t{
        width: 45px;
        display: flex;
        margin-top: 5px;
        border-top: 1px solid var(--heading-color);
    }
    .number{
        position: absolute;
        top: -5px;
        left: 55px;
    }
   }
    // end service
   &-wrap2{
        padding: 30px 20px;
        border-radius: 10px;
        transition: all .4s;
        margin-bottom: 24px;
        border: 1px solid var(--light-color2);
        a.bmix-icon-btn{
            font-weight: 700;
            font-size: 16px;
            line-height: 22px;
            transition: all 0.4s;
            color: var(--heading-color);
            svg{
                transition: all 0.4s;
                margin: 0 0 3px 3px;
            }
        }
        &:hover{
            border: 1px solid var(--accent-color);
            background: var(--accent-color);
            transform: rotate(-2deg);
            .bmix-iconbox-title-icon{
                svg path{
                    stroke: var(--white-color);
                    fill: var(--white-color);
                }
            }
            .bmix-iconbox-title-content{
                h5{
                    color: var(--white-color);
                }
            }
            .bmix-iconbox-text{
                p{
                    color: var(--white-color);
                }
            }
            a.bmix-icon-btn{
                color: var(--white-color);
                svg path{
                    fill: var(--white-color);
                }
            }
        }
   }
   &-title-wrap{
        display: flex;
        gap: 24px;
        align-items: center;
        margin-bottom: 15px;
   }
   &-title-content{
    h5{
        transition: all 0.4s;
    }
   }
   &-text{
        margin-bottom: 30px;
        p{
            transition: all 0.4s;
        }
    }

    &-wrapv3{
        ul{
            li{
                font-size: 20px;
                line-height: 28px;
                letter-spacing: -0.001em;
                font-weight: 700;
                color: var(--heading-color);
                margin-bottom: 15px;
                padding-left: 30px;
                position: relative;
                &:last-child{
                    margin-bottom: 0;
                }
                @include max (1199px){
                    padding-left: 24px;
                }
                @include max (991px){
                    padding-left: 30px;
                    text-align: left;
                }
                img{
                    width: 24px;
                    position: absolute;
                    top: 3px;
                    left: 0;
                }
            }
        }
        &.content-v5{
            ul{
                li{
                    img{
                        width: 21px!important;
                        margin-bottom: 2px;
                        @include max (1199px){
                            margin-right: 3px;
                        }
                    }
                }
            }
        }
    }
    // end v3
    &-wrap4{
        padding: 30px 26px;
        text-align: center;
        margin-bottom: 24px;
        border-radius: 5px;
        transition: all 0.4s;
        background: var(--white-color);
    }
    &-icon4{
        margin-bottom: 20px;
    }
    &-data4{
        h5{
            margin-bottom: 12px;
        }
    }
    &-content-wrapper{
        display: flex;
        gap: 40px;
        @include max (1199px){
            gap: 24px;
        }
        @include max (991px){
            gap: 35px;
        }
        @include max (767px){
            display: block;
        }
    }
    &-wrap5{
        padding: 30px 24px;
        border-radius: 5px;
        transition: all 0.4s;
        position: relative;
        display: block;
        overflow: hidden;
        z-index: 0;
        margin-bottom: 24px;
        background-size: cover;
        background-position: center;
        &:hover .bmix-iconbox-title5{
            h5, p{
                color: var(--white-color);
            }
        }
        &:hover  a.bmix-icon-btn{
            color: var(--white-color);
            path{
                fill: var(--white-color);;
            }
        }
        &:hover{
            background-size: cover;
        }
        &:hover .bmix-iconbox-icon5{
            path{
                fill: var(--white-color);
            }
        }
        &::before{
            content: "";
            left: 0;
            top: 0;
            z-index: -1;
            width: 100%;
            height: 100%;
            transition: all 0.4s;
            position: absolute;
            background: var(--white-bg);
        }
        &:hover::before{
            opacity: 0;
        }
        
        a.bmix-icon-btn{
            font-weight: 700;
            font-size: 16px;
            line-height: 22px;
            transition: all 0.4s;
            color: var(--heading-color);
            svg{
                margin: 0 0 3px 5px;
                path{
                    transition: all 0.4s;
                }
            }
        }
        
    }
    &-icon5{
        margin-bottom: 30px;
        path{
            transition: all 0.4s;
        }
    }
    &-title5{
        margin-bottom: 30px;
        h5{
            margin-bottom: 12px;
            transition: all 0.4s;
        }
        p{
            transition: all 0.4s;
        }
    }
    // end v4
    &-wrap6{
        padding: 30px 26px;
        border-radius: 5px;
        margin-bottom: 24px;
        background: var(--light-color2);
        border: 2px solid var(--light-color2);
    }
    &-icon6{
        margin-bottom: 30px;
        @include max (767px){
            margin-bottom: 15px;
        }
    }
    &-data6{
        h5{
            margin-bottom: 15px;
        }
    }

    &-wrap7{
        padding: 0px 40px;
        text-align: center;
        margin-bottom: 24px;
        @include max (1399px){
            padding: 0 16px;
        }
        @include max (1199px){
            padding: 0 30px;
        }
        @include max (991px){
            padding: 0;
        }
        @include max (767px){
            padding: 0 80px;
        }
        @include max (575px){
            padding: 0 50px;
        }
        @include max (425px){
            padding: 0;
        }
    }
    &-icon7{
        width: 70px;
        height: 70px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: var(--white-color);
        border-radius: 100px;
        margin: 0 auto;
        text-align: center;
    }
    &-data7{
        h5{
            margin: 30px 0 15px 0;
            color: var(--white-color);
            @include max (767px){
                margin: 30px 0 15px 0;
            }
        }
        p{
            color: var(--white-color);
        }
    }

    &-wrap8{
        display: flex;
        gap: 20px;
        padding: 30px 24px;
        position: relative;
        text-align: left;
        z-index: 1;
        @include max (991px){
            margin-bottom: 24px;
        }
        &::before{
            content: "";
            left: 0;
            top: 0;
            z-index: -1;
            width: 100%;
            height: 100%;
            border-radius: 5px;
            position: absolute;
            background: linear-gradient(225deg, #1263FF 0%, #27AFF9 100%);
        }
    }
    &-icon8{
        flex-shrink: 0;
    }
    &-data8{
        h5 {
            margin-bottom: 10px;
            color: var(--white-color);
        }
        p{
            color: var(--white-color);
        }
    }
    // v7
    &-wrapv7{
        padding: 30px 24px;
        border-radius: 5px;
        margin-bottom: 24px;
        transition: all 0.4s;
        border: 2px solid var(--light-color2);
        a.bmix-icon-btn {
            font-size: 16px;
            line-height: 22px;
            font-weight: 700;
            transition: all 0.4s;
            color: var(--heading-color);
            svg{
                width: 16px;
                height: 16px;
                transition: all 0.4s;
                margin: 0 0 5px 5px;
            }
        }
        &:hover{
            box-shadow: 0px 4px 80px 0px #00000014;
            border: 2px solid transparent;
            a.bmix-icon-btn{
                color: var(--accent-color);
                svg path{
                    fill: var(--accent-color);
                }
            }
        }
    }
    &-title-wrapv7{
        display: flex;
        align-items: center;
        padding-bottom: 30px;
        margin-bottom: 20px;
        justify-content: space-between;
        border-bottom: 1px solid var(--light-color2);
        @include max (575px){
            h5{
                font-size: 20px;
                padding-bottom: 20px
            }
        }
    }
    &-wrap9{
        ul{
            li{
                font-weight: 700;
                font-size: 24px;
                line-height: 32px;
                margin-bottom: 15px;
                letter-spacing: -0.005em;
                color: var(--heading-color);
                position: relative;
                padding-left: 40px;
                &:last-child{
                    margin-bottom: 0;
                }
                @include max (991px){
                    text-align: left;
                }
                img{
                    position: absolute;
                    top: 5px;
                    left: 0;
                    width: 24px;
                }
            }
        }
    }
    // 
    &-wrap10{
        gap: 20px;
        display: flex;
        border-radius: 5px;
        padding: 30px 22px;
        box-shadow: 0px 4px 80px 0px #00000014;
    }
    &-icon10{
        flex-shrink: 0;
    }
    &-data10{
        h5{
            margin-bottom: 10px;
        }
    }
    // end
    &-wrap11{
        display: flex;
        @include max (991px){
            display: block;
        }
    }

    &-wrap12{
        padding: 24px;
        transition: all 0.4s;
        border: 2px solid var(--light-color2);
        @include max (991px){
            margin-bottom: 24px;
        }
        @include max (767px){
            padding: 15px;
        }
        &:hover{
            box-shadow: 0px 4px 80px 0px #00000014;
            border: 2px solid var(--white-color);
            .bmix-blog-btn-icon2{
                color: var(--accent-color);
                svg path{
                    fill: var(--accent-color);
                }
            }
        }
    }
    &-icon12{
        margin-bottom: 30px;
    }
    &-title12{
        h5{
            margin-bottom: 10px;
        }
        p{
            margin-bottom: 30px;
            @include max (767px){
                margin-bottom: 15px;
            }
        }
        a.bmix-blog-btn-icon2{
            color: var(--heading-color);
        }
    }
    
    
}

.process-v7{
    h2{
        color: var(--white-color);
    }
    position: relative;
    &::before {
        content: "";
        left: 0;
        top: 0;
        z-index: -1;
        opacity: .9;
        width: 100%;
        height: 100%;
        position: absolute;
        background: linear-gradient(225deg, #1263FF 0%, #27AFF9 100%);
    }
}
.mb-15{
    @include max (767px){
        margin-bottom: 15px!important;
    }
}


.about-v4{
    ul{
        li{
            margin-bottom: 15px!important;
        }
    }
}

.about4{
    padding-bottom: 50px;
    border-bottom: 1px solid var(--light-color2);
    margin-bottom: 30px;
}

.mr-100{
    margin-right: 100px;
    @include max (1199px){
        margin-right: 0;
    }
}

.bmix-service-site-btn{
    svg path{
        transition: all 0.4s
    }
}

// strat v2 process section

.process-section{
    h2{
        color: var(--white-color);
    }
}
.bmix-process{
    &-wrap{
        text-align: center;
        transition: all 0.4s;
        margin-bottom: 24px;
        &:hover{
            .bmix-process-number h4{
                background: var(--white-bg);
            }
        }
    }
    &-contact-site{
        display: flex;
        gap: 15px;
        align-items: center;
    }
    &-number{
        display: flex;
        justify-content: center;
        h4{
            width: 80px;
            height: 80px;
            display: flex;
            transition: all 0.8s;
            align-items: center;
            border-radius: 100px;
            margin-bottom: 24px;
            justify-content: center;
            background: linear-gradient(225deg, #1263FF 0%, #27AFF9 100%);
        }
    }
    &-number2{
        ul{
            li{
                width: 60px;
                height: 60px;
                display: flex;
                align-items: center;
                border-radius: 100px;
                justify-content: center;
                background: linear-gradient(225deg, #1263FF 0%, #27AFF9 100%);
            }
        }
    }
    &-number3{
        h4{
            color: var(--white-color);
        }
    }
    &-title{
        h4{
            color: var(--white-color);
            margin-bottom: 15px;
        }
        p{
            color: var(--white-color);
        }
    }
}
// start v3
.mt-40{
    margin-top: 40px;
    @include max (767px){
        margin-top: 30px;
    }
}