.bmix-skill{
    &-wrap{
        h4{
            margin-bottom: 20px;
        }
        // team page
    }
    &-item{
        margin-bottom: 24px;
    }
    &-title{
        h6{
            margin-bottom: 12px;
            color: var(--heading-color);
        }
        
    }
    &-line{
        position: relative;
        height: 5px;
        background: var(--light-color4)!important;
        border-radius: 10px;
        transform: scaleX(0);
        transform-origin: left;
        animation: animate 1s cubic-bezier(1, 0, 0.5, 1) forwards;
        margin-bottom: 20px;
    }
    &-bar{
        height: 100%;
        position: absolute;
        border-radius: 10px;
        transform: scaleX(0);
        transform-origin: left;
        background: var(--accent-color);
        animation: animate 1s 1s cubic-bezier(1, 0, 0.5, 1) forwards;
        
    &::before{
        position: absolute;
        content: "";
        height: 0;
        right: 0;
        top: -14px;
        width: 0;
        border: 7px solid transparent;
        border-bottom-width: 0px;
        border-right-width: 0px;
        border-top-style: #f0f0f0;
    }
    &::after{
        position: absolute;
        right: -32px;
        top: -44px;
        color: var(--dark-bg);
        font-size: 20px;
        font-weight: 700;
        padding: 1px 8px;
        border-radius: 3px;
    }

    }
}

.bar-one{
    width: 70%;
}
.bar-one::after{
    content: "70%";
}
.bar-two{
    width: 95%;
}
.bar-two::after{
    content: "95%";
}
.bar-three{
    width: 87%;
}
.bar-three::after{
    content: "87%";
}



@keyframes animate {
    100% {
    transform: scaleX(1);
    }
}
