.bmix-footer{
    &-top {
        padding: 100px 0 80px;
        @include max (991px){
        padding: 80px 0 60px;
        }
        @include max (767px){
            padding: 60px 0 40px;
        }
    }

    &-top2 {
        padding: 100px 0;
        border-bottom: 1px solid var(--light-color2);
        margin-bottom: 100px;
        @include max (991px){
            padding: 80px 0;
            margin-bottom: 80px;
        }
        @include max (767px){
            padding: 60px 0 40px;
            margin-bottom: 60px;
        }
    }

    &-bottom{
        text-align: center;
        padding: 35px 0;
        border-top: 1px solid var(--light-color2);

        @include max(767px) {
            flex-wrap: wrap;
            gap: 20px;
            justify-content: center;
        }
    }

    &-bottom2{
        display: flex;
        align-items: center;
        padding-bottom: 100px;
        flex-wrap: wrap;
        justify-content: space-between;
        @include max (991px){
            padding-bottom: 80px;
        }
        @include max (767px){
            padding-bottom: 60px;
        }
        .bmix-logo-img{
            @include max (991px){
                margin-top: 40px;
            }
        }
    }
    &-bottom10{
        padding: 100px 0 30px 0;
        text-align: center;
        @include max (991px){
            padding: 80px 0 30px 0;
        }
        @include max (767px){
            padding: 60px 0 30px 0;
        }
    }
    &-top6{
        padding: 100px 0 70px 0;
        border-bottom: 1px solid var(--light-color2);
        @include max (991px){
            padding: 80px 0 60px 0;
        }
        @include max (767px){
            padding: 60px 0 40px;
        }
    }
    &-top7{
        padding: 100px 0 80px 0;
        border-bottom: 1px solid var(--light-color2);
        margin-bottom: 80px;
        @include max (991px){
            padding: 80px 0 60px 0;
        }
        @include max (767px){
            padding: 60px 0 40px;
        }
        .bmix-subcribe-logo{
            @include max (767px){
                margin-bottom: 30px;
            }
        }
    }
    &-menu{
        h5 {
            margin-bottom: 20px;
            @include max (767px){
                margin-bottom: 15px;
            }
        }
        ul {
            li{
                margin-bottom: 12px;
                &:last-child{
                    margin-bottom: 0;
                }
         
                a{
                    position: relative;
                    transition: all 0.4s;
                    color: var(--body-color);
                    &::before{
                        content: " ";
                        left: 0;
                        bottom: 0;
                        width: 0;
                        height: 1px;
                        position: absolute;
                        transition: all .4s;
                        background: var(--heading-color);
                    }
                    &:hover{
                        &::before{
                            width: 100%;
                        }
                    }
                   
                    img{
                        margin-right: 10px;
                    }
                }
            }
        }
        @include max (1199px){
            margin-top: 40px;
        }
        @include max (767px){
            margin-top: 30px;
        }
    }

    &-menu2{
        &:hover{
            ul{
                li{
                    a{
                        img{
                            transform: scale(1.1);
                        }
                    }
                }
            }
        }
        ul{
            li{
                a{
                    overflow: hidden;
                    img{
                        object-fit: cover;
                        transition: all .8s;
                    }
                }
            }
        }
        @include max (1199px) {
            margin-top: 40px;
        }
        @include max (767px) {
            margin-top: 30px;
        }
        h5{
            margin-bottom: 24px;
        }
    }

    &-menu4{
        @include max (1199px) {
            margin-top: 40px;
        }
        @include max (767px) {
            margin-top: 30px;
        }
        h5{
            margin-bottom: 24px;
        }
    }
}
