.bmix-subscription{
    &-field{
        position: relative;
        input{
           margin: 0;
           width: 100%;
           color: var(--heading-color);
           font-weight: 700!important;
           font-size: 16px;
           border-radius: 50px;
           padding: 15px 37% 15px 20px !important;
           transition: all .4s;
           letter-spacing: .5px;
           &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
              color: var(--heading-color);
            }
            &::-moz-placeholder { /* Firefox 19+ */
              color: var(--heading-color);
            }
            &:-ms-input-placeholder { /* IE 10+ */
              color: var(--heading-color);
            }
            &:-moz-placeholder { /* Firefox 18- */
              color: var(--heading-color);
            }
            &:focus{
                border: 1px solid var(--light-color2)!important;
            }
            @include max (1199px){
                padding: 15px 25% 15px 20px !important;
            }
            @include max (767px){
                padding: 15px 20px 15px 20px !important;
            }
        }
    }
    &-field2{
        position: relative;
        input{
           margin: 0;
           width: 100%;
           color: var(--heading-color);
           font-weight: 700!important;
           font-size: 16px;
           border-radius: 50px;
           padding: 15px 37% 15px 20px !important;
           transition: all .4s;
           letter-spacing: .5px;
           background: var(--light-color2);
           &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
              color: var(--heading-color);
            }
            &::-moz-placeholder { /* Firefox 19+ */
              color: var(--heading-color);
            }
            &:-ms-input-placeholder { /* IE 10+ */
              color: var(--heading-color);
            }
            &:-moz-placeholder { /* Firefox 18- */
              color: var(--heading-color);
            }
            &:focus{
                border: 1px solid var(--light-color2)!important;
            }
            @include max (1199px){
                padding: 15px 25% 15px 20px !important;
            }
            @include max (767px){
                padding: 15px 20px 15px 20px !important;
            }
        }
    }
}

.footer1-contact{
    &-data{
        @include max (1199px){
            margin-top: 30px;
        }
        h5{
            margin-bottom: 24px;
        }
        ul{
            li{
                margin-bottom: 25px;
                &.location{
                    position: relative;
                    padding-left: 25px;
                }
                img{
                    position: absolute;
                    top: 7px;
                    left: 0;
                }
                a{
                    position: relative;
                    padding-left: 25px;
                    color: var(--body-color);
                    img{
                        position: absolute;
                        top: 7px;
                        left: 0;
                    }
                }
            }
        }
    }
}