.integration-social-icon{
    text-align: center;
}
.medicx-integration-list-icon{
    &-wrap
    {
        display: flex;
        gap: 70px;
        align-items: center;
        @include max(767px){
            display: block;
        }
    }
    &-icon{
        ul{
            li{
                margin-bottom: 40px;
                font-size: 24px;
                font-weight: 700;
                line-height: 32px;
                text-align: left;
                color: #0e0e0e;
                &:nth-child(3){
                    margin-bottom: 0;
                }
                &:last-child{
                    margin-bottom: 0;
                }
                @include max(767px){
                    &:nth-child(2){
                        margin-bottom: 20px;
                    }
                }
                @include max(991px){
                    margin-bottom: 20px;
                }
                img{
                    margin-right: 12px;
                }
            }
        }
    }
}
// v7
.integrationv7{
    @include max (991px){
        margin-top: 40px;
        margin-bottom: 0!important;
    }
    @include max (767px){
        margin-top: 30px;
    }
}