.bmix-blog{
    &-wrap {
        border-radius: 10px;
        margin-bottom: 24px;
        transition: all 0.4s;
        background: var(--light-color2);
        &:hover{
            box-shadow: 0px 4px 80px 0px #00000014;
            background: var(--white-color);
            .bmix-blog-img img {
                transform: scale(1.1);
            }
        }
        &:hover{
            .bmix-blog-btn-icon{
                color: var(--accent-color)!important;
            }
            svg path{
                stroke: var(--accent-color);
            }
        }
    }
    &-img{
        position: relative;
        overflow: hidden;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all .8s;
        }
    }
    &-content{
        padding: 30px 24px;
        h5{
            margin-bottom: 10px;
        }
        p{
            color: var(--body-color);
        }
    }
  
    &-meta {
        margin-bottom: 24px;
        &.inner-blog-meta{
            margin-bottom: 30px;
            ul {
                li{
                    a{
                        color: var(--body-color);
                    }
                }
            }
        }
        ul{
            li{
                display: inline-block;
                font-size: 16px;
                line-height: 22px;
                color: var(--heading-color);
                position: relative;
                padding-left: 24px;
                margin-right: 15px;
                &:last-child{
                    margin-right: 0;
                }
                img{
                    position: absolute;
                    left: 0;
                    top: 2px;
                }
            }
        }
    }
    
    &-btn-icon{
        font-size: 16px;
        line-height: 28px;
        font-weight: 700;
        color: var(--heading-color);
        svg{
            width: 16px;
            height: 14px;
            margin: 0 0 5px 8px;
        }
        
    }
    // end v2

    &-wrap2 {
        border-radius: 10px;
        margin-bottom: 24px;
        transition: all 0.4s;
        padding: 20px;
        background: var(--white-color);
        border: 2px solid var(--light-color2);
        &:hover{
            box-shadow: 0px 4px 80px 0px #00000014;
            background: var(--white-color);
            border: 2px solid var(--white-color);
            .bmix-blog-img img {
                transform: scale(1.1);
            }
        }
        &:hover{
            .bmix-blog-btn-icon{
                color: var(--accent-color)!important;
            }
            svg path{
                stroke: var(--accent-color);
            }
        }
    }
    &-content2{
        h5{
            margin-bottom: 10px;
        }
        p{
            color: var(--body-color);
        }
    }
  
    &-meta2 {
        padding: 24px 0;
        ul{
            li{
                display: inline-block;
                font-size: 16px;
                line-height: 22px;
                color: var(--heading-color);
                position: relative;
                padding-left: 30px;
                margin-right: 15px;
                &:last-child{
                    margin-right: 0;
                }
                img{
                    position: absolute;
                    left: 0;
                    top: 2px;
                }
            }
        }
    }
    
    &-btn-icon{
        font-size: 16px;
        line-height: 22px;
        font-weight: 700;
        color: var(--heading-color);
        svg{
            width: 16px;
            height: 14px;
            margin: 0 0 5px 8px;
        }
        
    }
    // end v3
    // start v4
    &-wrap3{
        padding: 30px;
        border-radius: 5px;
        transition: all 0.4s;
        box-shadow: 0px 4px 80px 0px #00000014;
        &:hover{
            .bmix-blog-thumb img {
                transform: scale(1.1);
            }
        }
        &.blog-v8-wrap{
            padding: 20px;
        }
    }
    &-thumb{
        overflow: hidden;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all .4s;
        }
    }
    &-content3{
        h5{
            margin-bottom: 30px;
            padding-right: 30px;
            @include max (767px){
                margin-bottom: 20px;
            }
        }
        p{
            color: var(--body-color);
        }
        &.content-v4{
            h5{
                padding-right: 0;
            }
        }
    }
    
    
    &-meta3 {
        padding: 24px 0;
        &.metav4{
            padding: 0;
            margin-bottom: 15px;
        }
        ul{
            li{
                display: inline-block;
                font-size: 16px;
                line-height: 22px;
                color: var(--heading-color);
                position: relative;
                padding-left: 30px;
                margin-right: 15px;
                &:last-child{
                    margin-right: 0;
                }
                img{
                    position: absolute;
                    left: 0;
                    top: 2px;
                }
            }
        }
    }
    &-textv8{
        h5{
            margin-bottom: 10px;
        }
        p{
            color: var(--body-color);
        }
    }
    &-btn-icon2{
        font-size: 16px;
        line-height: 22px;
        font-weight: 700;
        transition: all 0.4s;
        color: var(--accent-color);
        svg{
            width: 16px;
            height: 14px;
            margin: 0 0 8px 8px;
        }
        &.icon-v4 a{
            font-family: $body-font!important;
            color: var(--heading-color);
        }
    }

    &-wrap4{
        gap: 20px;
        padding: 24px;
        display: flex;
        transition: all 0.4s;
        border-radius: 5px;
        margin-bottom: 24px;
        position: relative;
        border: 2px solid var(--light-color2);
        background: var(--light-color2);
        @include max (575px){
            display: block;
        }
        &:first-child{
            @include max (1199px){
                margin-top: 40px;
            }
        }
        &:hover{
            .bmix-blog-btn-icon2 a{
                color: var(--accent-color);
                svg path{
                    stroke: var(--accent-color);
                }
            }
            .thumb-v4{
                transform: scale(1.1);
            }
        }
    }

    // blog-inner-page
    
    &-wrap5{
        border-radius: 10px;
        margin-bottom: 30px;
        padding: 24px;
        transition: all 0.4s;
        border: 2px solid var(--light-color2);
        &:hover{
            border-radius: 10px;
            border: 2px solid var(--white-color);
            box-shadow: 0px 4px 80px 0px #00000014;
            .bmix-blog-content4 a .bmix-blog-btn-icon{
                color: var(--accent-color);
                svg path{
                    stroke: var(--accent-color);
                }
            }
        }
        &:hover .bmix-blog-thumb img {
            transform: scale(1.1);
        }
    }
    &-content4{
        padding-top: 24px;
        h4{
            margin-bottom: 15px;
        }
        p{
            font-size: 20px;
            margin-bottom: 30px;
        }
        a.bmix-blog-btn-icon{
            font-size: 16px;
            line-height: 22px;
            font-weight: 700;
            transition: all 0.4s;
            color: var(--accent-color);
            path{
                transition: all 0.4s;
            }
        }
    }
    &-sidebar{
        padding-left: 20px;
        @include max (991px){
            padding-left: 0;
            margin-top: 60px;
        }
        @include max (767px){
            margin-top: 40px;
        }
    }
    &-widgets{
        border-radius: 10px;
        margin-bottom: 30px;
        padding: 30px;
        border: 2px solid var(--light-color4);
        &:first-child{
            border: none;
            padding: 0;
        }
        h3{
            margin-bottom: 24px;
        }
        .bmix-search-box{
            position: relative;
            input{
                border: 1px solid var(--light-color4)!important;
                padding: 20px 42px 20px 30px;
                height: auto;
                border-radius: 20px !important;
            }
            img{
                position: absolute;
                top: 28px;
                right: 20px;
            }
        }
        .bmix-recent-post-item {
            gap: 15px;
            display: grid;
            align-items: center;
            overflow: hidden;
            margin-bottom: 24px;
            grid-template-columns: 96px 1fr;
            &:last-child{
                margin-bottom: 0;
            }
            .bmix-recent-post-thumb{
                img{
                    width: 100%;
                    border-radius: 5px;
                    height: 100px;
                    object-fit: cover;
                }
            }
        }
        .bmix-recent-post-data{
            span{
                font-size: 16px;
                color: var(--body-color);
                margin-bottom: 5px;
                img{
                    margin-bottom: 5px;
                    margin-right: 10px;
                }
            }
            h6{
                font-size: 20px;
                line-height: 28px;
                letter-spacing: -0.001em;
            }
        }
        
    }
    &-categorie{
        ul{
            li{
                margin-bottom: 15px;
                &:last-child{
                    margin-bottom: 0;
                }
                a{
                    font-size: 21px;
                    color: var(--body-color);
                    line-height: 30px;
                    letter-spacing: -.001em;
                    transition: all .0.4s;
                    &:hover{
                        color: var(--accent-color);
                    }
                }
            }
        }
    }
    
}

.bmix-blog-tags{
    ul {
        display: flex;
        gap: 10px;
        flex-wrap: wrap;
        li{
      
            a{
                font-size: 16px;
                color: var(--heading-color);
                padding: 5px 20px;
                border-radius: 50px;
                transition: all 0.4s;
                background: #060E280D;
                display: block;
                &:hover{
                    color: var(--white-color);
                    background: var(--accent-color);
                }
            }
        }
    }
    &.blog-tags-social{
        display: flex;
        gap: 30px;
        align-items: center;
        @include max (991px){
            margin-top: 30px;
        }
        ul{
            li{
                margin-right: 15px;
                &:last-child{
                    margin-right: 0;
                }
                a{
                    background: none;
                    padding: 0;
                    svg{
                        transition: all 0.4s;
                    }
                    &:hover{
                        path{
                            fill: var(--accent-color);
                        }
                    }
                }
            }
        }
    }
}

.mb-20{
    margin-bottom: 20px;
}


.bmix-post-navigation-wrap{
    display: flex;
    gap: 24px;
    max-width: 300px;
    .title-post-content{
        ul{
            li{
                font-size: 15px!important;
                line-height: 16px;
                font-weight: 400;
                margin-bottom: 10px;
            }
        }
    }
}
.nav-preview{
    &-wrap{
        @include max(991px){
            margin-bottom: 24px;
            &:last-child{
                margin-bottom: 0;
            }
        }
        
    }
    &-icon{
        margin-bottom: 24px;
        a{
            color: var(--dark-bg);
            font-size: 13px;
            font-weight: 700;
            display: flex;
            align-items: center;
            gap: 10px;
        }
    }
}
.bmix-post-navigation-wrapper{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-bottom: 40px;
    border-bottom: 1px solid var(--light-color);
    margin-bottom: 40px;
}


.bmix-blog{
    &-thumb{
        border-radius: 10px;
        img{
            width: 100%;
        }
    }
    &-thumb2{
        margin-bottom: 30px;
        img{
            width: 100%;
        }
        &.blog-details{
            margin-bottom: 40px;
            @include max (767px){
                margin-bottom: 30px;
            }
        }
        &.thumb-v4{
            margin-bottom: 0;
            overflow: hidden;
            transition: all 0.4s;
            @include max (575px){
                margin-bottom: 20px;
            }
        }
    }
}
.bmix-single-post-content-wrap{
    .bmix-blog-entry{
        &-content{
            .bmix-blog-quote-content{
                &-wrap{
                    padding: 30px;
                    margin-top: 30px;
                    margin-bottom: 24px;
                    border-radius: 10px;
                    background: var(--accent-color);
                    h4{
                        color: var(--white-color);
                    }
                }
                &-wrap2{
                    margin-top: 40px;
                }
                &-text2{
                    margin-bottom: 24px;  
                    &:last-child{
                        margin-bottom: 40px;
                        @include max (767px){
                            margin-bottom: 30px;
                        }
                    }
                    span{
                        font-weight: 400;
                        font-size: 20px;
                        line-height: 32px;
                        color: var(--body-color);
                    }
                }
            }
            .form-content-box{
                h3{
                    margin-bottom: 30px;
                }
                .bmix-content-wrapper{
                    padding: 30px;
                    border-radius: 10px;
                    background: var(--light-color2);
                    .bmix-main-field{
                        margin-bottom: 24px;
                        input{
                            font-size: 16px;
                            font-weight: 400;
                            line-height: 22px;
                            letter-spacing: -0.01em;
                            text-align: left;
                        }
                    }
                }
            }
        }
    }
}


.bmix-blog-tag{
    &-wrapper {
        display: flex;
        padding: 24px 0;
        justify-content: space-between;
        border-top: 2px solid var(--light-color2);
        border-bottom: 2px solid var(--light-color2);
       
        @include max (991px){
            display: block;
        }
        @include max (767px){
            padding-bottom: 30px;
            margin-bottom: 30px;
        }
    }
    &-wrap{
        display: flex;
        gap: 30px;
        align-items: center;
        ul{
            li{
                margin-right: 30px;
                &:last-child{
                    margin-right: 0;
                }
            }
        }
        @include max (991px){
            h5{
                margin-bottom: 20px;
            }
        }
    }
}

.bmix-post-comment{
    margin-top: 40px;
    @include max (767px){
        margin-top: 30px;
    }
    h3{
        margin-bottom: 24px;
    }
    ul{
        li{
            
            &.bmix-post-comment-wrap {
                display: flex;
                gap: 15px;
                &:last-child{
                    padding-bottom: 40px;
                    border-bottom: 2px solid var(--light-color2);
                    margin-bottom: 40px;
                }
                .bmix-post-comment-thumb{
                    img{
                        width: 100%;
                    }
                }
                &.children{
                    padding-top: 30px;
                    border-top: 2px solid var(--light-color2);
                    margin-top: 30px;
                    padding-bottom: 30px;
                    border-bottom: 2px solid var(--light-color2);
                    margin-bottom: 30px;
                    margin-left: 85px;
                    
                }
                .bmix-post-comment{
                    &-data{
                        span{
                            font-size: 16px;
                            font-weight: 400;
                            line-height: 22px;
                            letter-spacing: -0.01em;
                            text-align: left;
                        }
                        position: relative;
                        max-width: 650px;
                        @include max (1199px){
                            max-width: 470px;
                        }
                        @include max (767px){
                            max-width: 330px;
                        }
                        .bmix-comment-reply{
                            position: absolute;
                            bottom: 85px;
                            right: -60px;
                            font-weight: 700;
                            font-size: 16px;
                            color: var(--heading-color);
                            transition: all 0.4s;
                            @include max (1199px){
                                max-width: 470px;
                                bottom: 115px;
                                right: -45px;
                            }
                            @include max (991px){
                                right: -75px;
                            }
                            @include max (767px){
                                bottom: 180px;
                                right: -65px;
                            }
                            @include max (425px){
                                right: 30px;
                            }
                            &:hover{
                                color: var(--accent-color);
                            }
                        }
                    }
                    &-data2{
                        position: relative;
                        max-width: 593px;
                        span{
                            font-size: 16px;
                            font-weight: 400;
                            line-height: 22px;
                            letter-spacing: -0.01em;
                            text-align: left;
                        }
                        @include max (1199px){
                            max-width: 418px;
                        }
                        @include max (767px){
                            max-width: 300px;
                        }
                        .bmix-comment-reply2{
                            position: absolute;
                            bottom: 85px;
                            right: -60px;
                            font-weight: 700;
                            font-size: 16px;
                            color: var(--heading-color);
                            transition: all 0.4s;
                            @include max (1199px){
                                bottom: 120px;
                                right: -45px;
                            }
                            @include max (991px){
                                right: -75px;
                            }
                            @include max (767px){
                                bottom: 150px;
                                right: -50px;
                            }
                            @include max (425px){
                                bottom: 180px;
                                right: 24px;
                            }
                            &:hover{
                                color: var(--accent-color);
                            }
                        }
                    }
                    
                }
                p{
                    margin-top: 15px;
                }
            }
            &.bmix-post-comment-wrap2{
                padding-bottom: 50px;
                border-bottom: 1px solid rgba(0, 34, 255, 0.1);
                margin-bottom: 50px;
            }
        }
    }
}

