.medicx-countdown{
    &-wrap{
        display: flex;
        justify-content: center;
        @include max (1450px){
            flex-wrap: wrap;
            gap: 24px;
        }
        
    }
    &-item{
        padding: 34px 60px;
        border-radius: 10px 0 0 10px;
        background: var(--accent-color);
        @include max (1450px){
            border-radius: 10px;
        }
        &.border-radius-0{
            border-radius: 0;
            @include max (1450px){
                border-radius: 10px;
            }
        }
        .number{
            display: flex;
            align-items: center;
            justify-content: center;
        }
        p{
            font-weight: 600;
            font-size: 24px;
            line-height: 36px;
            color: var(--white-color);
            text-align: center;
            margin-top: 10px;
        }
        .countdown1{
            .number{
                width: 200px;
                height: 200px;
                font-weight: 700;
                font-size: 120px;
                line-height: 1em;
                border-radius: 100px;
                text-align: center;
                color: var(--accent-color);
                background-color: #fff;
                
                
            }
        }
     
    }
    &-item1{
        padding: 34px 60px;
        border-radius: 10px 0 0 10px;
        background: var(--heading-color);
        
        &.border-radius-0{
            border-radius: 0;
            @include max (1450px){
                border-radius: 10px;
            }
        }
        &.border-radius-1{
            border-radius: 0 10px 10px 0;
            @include max (1450px){
                border-radius: 10px;
            }
        }
        .number{
            display: flex;
            align-items: center;
            justify-content: center;
        }
        p{
            font-weight: 600;
            font-size: 24px;
            line-height: 36px;
            color: var(--white-color);
            text-align: center;
            margin-top: 10px;
        }
        .countdown1{
            .number{
                width: 200px;
                height: 200px;
                font-weight: 700;
                font-size: 120px;
                line-height: 1em;
                border-radius: 100px;
                text-align: center;
                color: var(--accent-color);
                background-color: #fff;
            }
        }
    }
}


#medicx-subscription-btn {
    @media (max-width: 767px) {
        position: static!important;
        width: 100%!important;
        margin-top: 24px;
    }
}