.bmix-service{
    &-wrap{
        padding: 30px 20px;
        border-radius: 10px;
        transition: all 0.4s;
        margin-bottom: 24px;
        background: var(--light-color2);
        border: 1px solid var(--light-color2);
        a.bmix-icon-btn {
            font-size: 16px;
            line-height: 22px;
            font-weight: 700;
            transition: all 0.4s;
            color: var(--heading-color);
            svg{
                width: 16px;
                height: 16px;
                transition: all 0.4s;
                margin: 0 0 5px 5px;
            }
        }
        &:hover{
            box-shadow: 0px 4px 80px 0px #00000014;
            border: 1px solid var(--white-color);
            background: var(--white-color);
            a.bmix-icon-btn{
                color: var(--accent-color);
                svg path{
                    fill: var(--accent-color);
                }
            }
        }
    }
    &-text{
        margin-bottom: 30px;
    }
    &-title-wrap{
        display: flex;
        align-items: center;
        margin-bottom: 10px;
        justify-content: space-between;
    }
    &-site-btn{
        @include max (575px){
            a svg{
                width: 80%;
            }
        }
    }
    // start v6
    &-wrapv6{
        padding: 30px;
        border-radius: 3px;
        transition: all 0.4s;
        margin-bottom: 24px;
        background: var(--white-bg);
        &:hover{
            box-shadow: 0px 4px 80px 0px #00000014;
            a.bmix-service-site-btn{
                background: var(--accent-color);
                svg path{
                    fill: var(--white-color);
                }
            }
            a.bmix-service-site-btn2{
                background: var(--accent-color);
                svg path{
                    fill: var(--white-color);
                }
            }
        }
        h4{
            margin-bottom: 30px;
            @include max (767px){
                margin-bottom: 20px;
            }
            @include max (575px){
                margin-bottom: 10px;
            }
        }
    }
    &-content-wrap{
        h4{
            margin-bottom: 15px;
        }
    }
    &-titlev6{
        display: flex;
        justify-content: space-between;
        @include max (767px){
            align-items: center;
        }
    }
    &-site-btn2{
        width: 50px;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 100px;
        border: 1px solid var(--accent-color);
    }
    &-details{
        h3{
            margin-bottom: 10px;
        }
        &-data{
            padding: 20px 0;
            ul{
                li{
                    font-weight: 600;
                    margin-bottom: 5px;
                    &:last-child{
                        margin-bottom: 0;
                    }
                }
            }
        }
    }
}
.mr-52{
    margin-right: 50px;
    @include max (991px){
        margin-right: 0;
    }
}

.pb-30{
    padding-bottom: 30px;
}

.service-v4{
    h2{
        color: var(--white-color);
    }
}
// start v5
.bmix-service-table{
    &-item{
        display: flex;
        padding: 30px 0;
        border-bottom: 1px solid var(--body-color);
        &:first-child{
            padding-top: 0;
        }
        &:last-child{
            padding-bottom: 0;
            border: none;
        }
        @include max(991px) {
            display: block;
        }
        &.item2{
            border-bottom: 1px solid rgba(30, 38, 61, 0.1);
            &:last-child{
                border: none;
            }
        }
    }
    &-title{
        flex: 0 0 36%;
        h3{
            margin: 0;
            font-size: 35px;
            @include max(1199px) {
                font-size: 28px;
            }
            @include max(991px) {
                font-size:30px;
            }
        }
    }
    &-body{
        flex: 0 0 45%;
        padding: 0 40px;
        @include max(991px) {
            padding: 24px 0;
        }
        p{
            font-size: 20px;
        }
    }
    &-btn{
        flex: 0 0 19%;
        text-align: right;
        @include max(991px) {
            text-align: left;
        }
        .bmix-default-btn.outline-btn{
            padding: 17px 72px;
        }
        &:hover  .bmix-default-btn.outline-btn{
            background-color: var(--accent-color);
            border: 1px solid var(--accent-color);
        }
    }

    
}
.service-v5{
    h2,h4,p{
        color: var(--white-color);
    }
}
.mb-31{
    @include max (991px){
        margin-bottom: 30px;
    }
}