header.bmix-header-section {
  position: fixed;
  z-index: 9;
  width: 100%;
  top: 0;
  transition: all 0.4s;
}
// Bmix project

.bmix-header-wrap{
  border-left: 1px solid var(--light-color2);
  @include max(991px) {
    border-left: 0;
  }
}
.bmix-header{
  &-top {
    transition: all 0.4s;
  }
  &-info-wrap{
    display: flex;
    padding: 10px 0;
    padding-left: 28px;
    justify-content: space-between;
    border-bottom: 1px solid var(--light-color2);
    @include max (1199px){
      display: none;
    }
  }
  &-info-data {
    ul{
      li{
        display: inline-block;
        font-size: 16px;
        font-weight: 400;
        margin-right: 20px;
        &.mb-0{
          margin-right: 0;
        }
        color: var(--light-color);
        img{
          margin-right: 5px;
          margin-bottom: 2px;
        }
        a{
          color: var(--light-color);
          img{
            margin-right: 5px;
            margin-bottom: 2px;
          }
        }
      }
    }
  }
  &-bottom {
    padding: 10px 0;    
  }
    // end header one
  &-top2 {
    padding: 20px 0;
    transition: all 0.4s;
    background: var(--dark-bg);
    @include max (991px){
      display: none;
    }
  }
  &-info-wrap2{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &-info-data2 {
    ul{
      li{
        display: inline-block;
        font-size: 16px;
        font-weight: 400;
        margin-right: 20px;
        &.mb-0{
          margin-right: 0;
        }
        color: var(--white-color);
        img{
          margin-right: 5px;
          margin-bottom: 2px;
        }
        a{
          color: var(--white-color);
          img{
            margin-right: 5px;
            margin-bottom: 2px;
          }
        }
      }
    }
  }
  &-bottom2{
    padding: 15px 0;
  }

  // end header two

  &-top3{
    background: none;
    padding: 20px 0;
    transition: all 0.4s;
    @include max (991px){
      display: none;
    }
  }
  &-info-wrap3{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &-info-data3 {
    ul{
      li{
        font-size: 16px;
        font-weight: 400;
        margin-right: 24px;
        padding-right: 24px;
        color: var(--white-color);
        display: inline-block;
        &.mb-0{
          margin-right: 0;
        }
        &:last-child{
          margin-right: 0;
        padding-right: 0;
        }
      
        img{
          margin-right: 5px;
          margin-bottom: 2px;
        }
        a{   
          color: var(--white-color);
          img{
            margin-right: 5px;
            margin-bottom: 2px;
          }
        }
      }
    }
  }
  &-bottom3{
    padding: 20px 0;
    background: var(--white-color);
  }
  // end three
  &-top5 {
    padding: 20px 0;
    transition: all 0.4s;
    background: var(--accent-color);
    @include max (991px){
      display: none;
    }
  }
}


.w_50{
  @include max(575px) {
    width: 50%;
  }
}
.sticky-menu{
  .bmix-header-top{
    margin-top: -55px;
    transition: all .4s;
    @include max (1399px){
      margin-top: -85px;
    }
    @include max (1199px){
      margin-top: 0;
    }
  }
  .bmix-header-top2 {
    margin-top: -72px;
    transition: all .4s;
    @include max (1199px){
      margin-top: -90px;
    }
  }
  .bmix-header-top3 {
    margin-top: -72px;
    transition: all .4s;
    @include max (1199px){
      margin-top: -90px;
    }
  }
  .bmix-header-top5 {
    margin-top: -72px;
    transition: all .4s;
    @include max (1199px){
      margin-top: -90px;
    }
  }
  // v5
  .bmix-header-bottom2{
    background: var(--heading-color);
  }
}

.border{
  &-right {
    position: absolute;
    top: 30%;
    left: 158px;
    display: block;
    height: 15px;
    border-right: 1px solid var(--light-color2);
    opacity: 20%;
    @include max (1199px){
      display: none;
    }
  }
  &-right2 {
    position: absolute;
    top: 30%;
    left: 41.5%;
    display: block;
    height: 15px;
    border-right: 1px solid var(--light-color2);
    opacity: 20%;
    @include max (1199px){
      display: none;
    }
  }
}

.white-color{
  color: var(--white-color)!important;
}