.bmix-pricing{
  &-wrap{
    padding: 40px;
    border-radius: 10px;
    margin-bottom: 24px;
    border: 2px solid var(--light-color2);
    &.active{
      border: none;
      box-shadow: 0px 4px 80px 0px #00000014;
    }
  }
  &-header{
    margin-bottom: 30px;
    @include max (767px){
      margin-bottom: 15px;
    }
    h4{
      margin-bottom: 20px;
      @include max (767px){
        margin-bottom: 15px;
      }
    }
  }
  &-price{
    display: flex;
    margin-bottom: 30px;
    span{
      font-size: 24px;
      line-height: 32px;
      color: var(--heading-color);
    }
    p{
      display: flex;
      align-items: end;
    }
  }
  &-btn{
    margin-bottom: 30px;
  }
  &-body{
    ul{
      li{
        margin-bottom: 15px;
        position: relative;
        padding-left: 30px;
        &:last-child{
          margin-bottom: 0;
        }
        img{
          position: absolute;
          top: 5px;
          left: 0;
        }
      }
    }
  }
}

.mb-0{
  margin-bottom: 0;
}

.bmix-pricing-table{
  &-item{
    display: flex;
    padding: 30px;
    align-items: center;
    border-radius: 10px;
    transition: all 0.4s;
    margin-bottom: 24px;
    position: relative;
    background: var(--light-color2);
    border: 2px solid var(--light-color2);
    &:hover {
      box-shadow: 0px 4px 80px 0px #00000014;
      background: var(--white-bg);
      border: 2px solid var(--white-color);
      .bmix-default-btn.outline-btn{
        background-color: var(--accent-color);
        border: 1px solid var(--accent-color);
        color: var(--white-color)!important;
      }
      .bmix-pricing-badge {
        opacity: 1;
        position: absolute;
        top: 20px;
        right: 0;
        transition: all 0.4s;
        display: block;
        @include max (991px){
          display: none;
        }
      }
    }
    @include max(991px) {
      display: block;
    }
    &.item2{
      background: var(--white-bg);
    }
  }
  &-header{
    flex: 0 0 36%;
    @include max (1199px){
      flex: 0 0 30%;
    }
    h4{
      margin-bottom: 38px;
      @include max (767px){
        margin-bottom: 15px;
      }
    }
  }
  &-body{
      flex: 0 0 45%;
      padding: 0 40px;
      @include max (1199px){
        flex: 0 0 45%;
        padding: 0 30px;
      }
      @include max(991px) {
          padding: 24px 0;
      }
      p{
          font-size: 20px;
      }
  }
  &-btn{
    flex: 0 0 19%;
    text-align: right;
    @include max (1199px){
      flex: 0 0 25%;
    }
    @include max(991px) {
        text-align: left;
    }
    .bmix-default-btn.outline-btn{
        padding: 18px 34px;
        color: var(--accent-color)!important;
        border: 1px solid var(--accent-color);
    }
  }
}
.bmix-pricing-badge{
  display: none;
  transition: all 0.4s;
}