.cta-sectionv1{
    h2,p{
        color: var(--white-color);
    }
}

.bmix-cta{
    &-contentv4{
        max-width: 750px;
        text-align: center;
        margin: 0 auto;
        h2,p{
            color: var(--white-color);
        }
    }
    &-v6{
        position: relative;
        z-index: 0;
        background-size: cover;
        background-position: center;
        &::before{
            content: "";
            left: 0;
            top: 0;
            z-index: -1;
            opacity: 0.9;
            width: 100%;
            height: 100%;
            position: absolute;
            background: var(--heading-color);
        }
    }
    &-v10{
        max-width: 800px;
        text-align: center;
        margin: 0 auto;
        h2,p{
            color: var(--white-color);
        }
    }
    &-10{
        position: relative;
        z-index: 0;
        background-size: cover;
        background-position: center;
        &::before{
            content: "";
            left: 0;
            top: 0;
            z-index: -1;
            opacity: 0.9;
            width: 100%;
            height: 100%;
            position: absolute;
            background: var(--heading-color);
        }
    }
}