.bmix-accordion{
  &-item{
    padding: 20px;
    position: relative;
    margin-bottom: 24px;
    border-radius: 10px;
    transition: all 0.4s;
    border: 1px solid var(--body-color);
    &.open{
      border: 1px solid var(--body-color);
      
    }
    &.open{
      .bmix-accordion-body{
        display: block;
      }
    }
  }
  &-item1{
    margin-bottom: 24px;
    &.open{
      .bmix-accordion-body{
        display: block;
       p{
          padding: 0 24px;
          color: var(--body-color);
          font-size: 20px!important;
          @include max (575px){
            padding: 0 26px;
          }
       }
      }
      .header1{
        h5{
          color: var(--accent-color);
        }
      }
    }
  }
  &-item2{
    padding: 24px;
    border-radius: 5px;
    transition: all 0.4s;
    margin-bottom: 24px;
    border: 2px solid var(--light-color2);
    &.open{
      box-shadow: 0px 4px 80px 0px #00000014;
      border: 2px solid var(--white-color);
      @include max (991px){
        margin-top: 30px;
      }
      .accordion-header-v7{
        background: var(--accent-color);
        margin: -26px -26px 0 -26px;
        border-radius: 5px 5px 0 0;
        padding: 24px;
        h5{
          color: var(--white-color);
        }
        .bmix-active-iconv7{
          width: 30px;
          height: 30px;
          border: 1px solid var(--white-color);
          transform: rotate(180deg);
          svg path{
            fill: var(--white-color);
          }
        }
      }
      .bmix-accordion-body{
        display: block;
      }
    }
  }
  &-item3{
    padding: 24px;
    border-radius: 10px;
    transition: all 0.4s;
    margin-bottom: 24px;
    border: 2px solid var(--light-color2);
    &.open{
      background: var(--accent-color);
      border: 2px solid var(--white-color);
      @include max (991px){
        margin-top: 30px;
      }
      .header2{
        h4{
          color: var(--white-color);
        }
      }
      .body2{
        p{
          color: var(--white-smoke2);
        }
      }
      
      .bmix-accordion-body{
        display: block;
      }
    }
  }    
  &-header{
    cursor: pointer;
    position: relative;
    display: flex;
    justify-content: space-between;
    h5{
      color: var(--white-color);
    }
    &.header1{
      justify-content: start;
      h5{
        color: var(--heading-color);
        @include max (575px){
          padding-left: 5px;
        }
      }
    }
    &.accordion-header-v7{
      h5{
        color: var(--heading-color);
      }
      .bmix-active-iconv7{
        width: 30px;
        height: 30px;
        display: flex;
        align-items: center;
        transition: all 0.4s;
        border-radius: 100px;
        justify-content: center;
        border: 1px solid var(--accent-color)
      }
    }
  }
  &-body{
    display: none;
    padding-top: 15px;
    p{
      color: var(--white-color);
      padding: 0 20px 0 0;
    }
    &.default{
      display: block;
    }
    @include max(991px) {
      padding: 24px 0 0;
    }
    @include max(767px) {
      padding: 18px 0 0;
    }
    &.accordion-body-v7{
      padding-top: 20px;
      p{
        color: var(--white-smoke)!important;
        padding-right: 0;
      }
    }
  }
  // End one
}
.bmix-accordion-wrapper {
  display: flex;
  gap: 24px;
  @include max (991px){
      display: block;
  }
}
.bmix-active-icon{
  display: flex;
  align-items: center;
  justify-content: center;
}

.bmix-accordion-section-wrapper{
  display: flex;
  gap: 24px;
  @include max (991px){
    display: block;
  }
}
.bmix-accordion-section-v3 {
  flex: 0 0 50%;
}
.accordion-button{
  color: var(--dark-bg)!important;
  background-color: var(body-color)!important;
  padding: 0;
}

.bmix-accordion-item.open {
.version-4-faq{
  img.plasicon{
    opacity: 0!important;
  }
  img.mynusicon{
    opacity: 1;
    margin-right: 10px;
    display: block;
  }
}
}

img.mynusicon {
opacity: 0;
}
img.plasicon {
opacity: 1;
margin-left: 10px;
}
.faq-v4{
  h2{
    color: var(--white-color);
  }
}

// start one
.bmix-accordion-item1.open {
  .version-1-faq{
    img.plasicon1{
      opacity: 0!important;
    }
    img.mynusicon1{
      opacity: 1;
      margin-right: 10px;
      margin-top: -15px;
      display: block;
    }
  }
  }
  
img.mynusicon1 {
  opacity: 0;
  margin-right: -10px;
}
img.plasicon1 {
  opacity: 1;
}
.faq-v4{
  h2{
    color: var(--white-color);
  }
}
  
// about-us
.bmix-accordion-item3.open {
    img.plasicon3{
      opacity: 0!important;
    }
    img.mynusicon3{
      opacity: 1;
      display: block;
    }
  
  }
  
img.mynusicon3 {
  opacity: 0;
  position: relative;
}
img.plasicon3 {
  opacity: 1;
  position: absolute;
}