
.bmix-features{
    &-content{
        padding: 30px;
        text-align: center;
        border-radius: 5px;
        transition: all 0.4s;
        @include max (767px){
            padding: 20px;
        }
        &:hover{
            box-shadow: 0px 4px 80px 0px #00000014;
        }
    }
    &-icon{
        margin-bottom: 15px;
    }
    &-data{
        h5{
            margin-bottom: 15px;
        }
        p{
            font-size: 18px;
            line-height: 26px;
            letter-spacing: -0.001em;
        }
    }
    
}

.bmix-features-section-v7{
    margin-top: -90px;
    background: #F5F5F5;
    @include max (991px){
        margin-top: -70px;
        padding-bottom: 60px;
    }
    @include max (767px){
        margin-top: -100px;
        padding-bottom: 42px;
    }
}