.bmix-navigation {
    margin-top: 80px;
    text-align: center;
    @include max(991px) {
        margin: 60px 0 0 0;
    }
    @include max(767px) {
        margin: 40px 0 0px 0;
    }
}

.bmix-navigation span.current, 
.page-links .current {
cursor: not-allowed;
    background-color: var(--accent-color);
    color: var(--white-color);
    border: 1px solid var(--accent-color);
}
.bmix-navigation a, 
.bmix-navigation span, 
.page-links a, 
.page-links span {
    height: 50px;
    width: 50px;
    display: inline-flex;
    text-align: center;
    font-weight: 700;
    line-height: 32px;
    font-size: 20px;
    align-items: center;
    justify-content: center;
    margin-left: 15px;
    color: var(--heading-color);
    border-radius: 100px;
    background: var(--light-bg);
    border: 1px solid var(--heading-color);
    &:first-child{
        margin-left: 0;
    }
}

.bmix-navigation a:hover, 
.page-links a:hover {
    background-color:  var(--accent-color);
    color: var(--white-color);
    border: 1px solid var(--accent-color);
    svg path{
        fill: var(--white-color);
    }
}
.pagination{
    justify-content: center;
}
i.ri-arrow-right-s-line {
    margin-left: 5px;
}