.bmix-contact-info{
    &-wrapper{
        display: flex;
        align-items: center;
        gap: 100px;
        @include max (1199px){
            gap: 70px;
        }
        @include max (1199px){
            gap: 45px;
        }
        @include max (575px){
            display: block;
        }
    }
    &-icon{
        margin-bottom: 24px;
        @include max (767px){
            margin-bottom: 15px;
        }
    }
    &-data{
        h5{
            color: var(--white-color);
            margin-bottom: 12px;
        }
        p{
            color: var(--light-color3);
        }
        ul{
            li{
                a{
                    color: var(--light-color3);
                }
            }
        }
    }
    &-wrap2{
        display: flex;
        gap: 24px;
        border-radius: 10px;
        padding: 30px 70px;
        margin-bottom: 50px;
        background: var(--light-color2);
        @include max (991px){
            margin-bottom: 40px;
        }
        @include max (767px){
            margin-bottom: 30px;
        }
    }
    &-data2{
        h4{
            margin-bottom: 10px;
        }
        ul{
            li{ color: var(--body-color);
                a{
                    color: var(--body-color);
                }
            }
        }
    }
}
.mr-105{
    margin-right: 105px;
    @include max (1399px){
        margin-right: 50px;
    }
    @include max (1199px){
        margin-right: 0;
    }
    @include max (991px){
        margin-right: 105px;
    }
    @include max (767px){
        margin-right: 0;
    }
    @include max (575px){
        margin-top: 24px;
    }
}

.b-right5 {
    position: absolute;
    top: 22%;
    left: 38%;
    display: block;
    height: 120px;
    border-right: 1px solid var(--body-color);
    @include max (1399px){
        left: 41%;
    }
    @include max (1399px){
        left: 45%;
    }
    @include max (991px){
        left: 37%;
    }
    @include max (767px){
        display: none;
    }
}
.responsive-map {
    overflow: hidden;
    position: relative;
    height: 730px;
    border-radius: 10px;
}
.responsive-map iframe{
    left:0;
    top:0;
    height:100%;
    width:100%;
    position:absolute;
}
