.bmix-hero-thumb{
    @include max (991px){
        margin-bottom: 40px;
    }
}
.bmix-about{
    &-card{
        position: absolute;
        top: 190px;
        right: -15px;
        overflow: hidden;
        transform: translatey(0px);
        -webkit-animation: updown 5s ease-in-out infinite;
        animation: updown 5s ease-in-out infinite;
        @include max (1199px){
            display: none;
        }
    }
}
// content v1

.bmix-content{
    &-thumb{
        text-align: center;
        @include max (991px){
            margin-bottom: 40px;
        }
    }
    &-card {
        top: 40%;
        left: -42px;
        overflow: hidden;
        position: absolute;
        transform: translatey(0px);
        animation: updown 5s ease-in-out infinite;
        -webkit-animation: updown 5s ease-in-out infinite;
        @include max (1199px){
            display: none;
        }
    }
}
