


.bmix-loading-dots {
  text-align: center;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--dark-bg);
  
  &--dot {
    animation: dot-keyframes 1.5s infinite ease-in-out;
    background-color: var(--accent-bg);
    border-radius: 10px;
    display: inline-block;
    height: 12px;
    width: 12px;
    margin: 0 7px;
    
    &:nth-child(2) {
      animation-delay: .5s;
    }
    
    &:nth-child(3) {
      animation-delay: 1s;
    }
  }
}

@keyframes dot-keyframes {
  0% {
    opacity: .4;
    transform: scale(1, 1);
  }

  50% {
    opacity: 1;
    transform: scale(1.2, 1.2);
  }

  100% {
    opacity: .4;
    transform: scale(1, 1);
  }
}


// \



.bmix-preloader-wrap {
  position: fixed;
  z-index: 1222;
  width: 100%;
  height: 100%;
  top: 0;
  background-color: var(--dark-bg);
  display: flex;
  justify-content: center;
  align-items: center;
}

.bmix-preloader {
  display: inline-block;
  position: relative;
  width: 74px;
  height: 100px;
  align-items: center;
  justify-content: center;
}

.bmix-preloader div {
  position: absolute;
  top: 33px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: var(--accent-color);
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}

.bmix-preloader div:nth-child(1) {
  left: 8px;
  animation: bmix-preloader1 0.6s infinite;
}

.bmix-preloader div:nth-child(2) {
  left: 8px;
  animation: bmix-preloader2 0.6s infinite;
}

.bmix-preloader div:nth-child(3) {
  left: 32px;
  animation: bmix-preloader2 0.6s infinite;
}

.bmix-preloader div:nth-child(4) {
  left: 56px;
  animation: bmix-preloader3 0.6s infinite;
}

@keyframes bmix-preloader1 {
  0% {
      transform: scale(0);
  }

  100% {
      transform: scale(1);
  }
}

@keyframes bmix-preloader3 {
  0% {
      transform: scale(1);
  }

  100% {
      transform: scale(0);
  }
}

@keyframes bmix-preloader2 {
  0% {
      transform: translate(0, 0);
  }

  100% {
      transform: translate(24px, 0);
  }
}