.bmix-teamv1{
    h2{
        color: var(--white-color);
    }
}
.bmix-team{
    &-wrap{
        border-radius: 10px;
        margin-bottom: 24px;
        padding: 40px 30px 30px 40px;
        background: var(--body-color);
        &:hover{
            .bmix-team-shape{
                &::before{
                    border: 1px solid var(--accent-color2);
                }
                &::after{
                    background: var(--accent-color2);
                }
            }
        }
        &.team8{
            padding: 24px;
        }
        &.team9{
            padding: 24px;
            transition: all 0.4s;
            background: var(--light-color2);
            &:hover{
                box-shadow: 0px 4px 80px 0px #00000014;
                background: var(--white-bg);
            }
        }
    }
    &-thumb{
        z-index: 1;
        position: relative;
        border-radius: 10px;
        &.bmix-team-shape{
            &::before{
                content: '';
                left: -10px;
                top: -10px;
                width: 97%;
                height: 101.5%;
                z-index: -1;
                transition: all 0.4s;
                position: absolute;
                border-radius: 10px;
                border: 1px solid var(--light-color4);
            }
            &::after{
                content: '';
                left: -10px;
                top: -10px;
                z-index: -1;
                width: 140px;
                height: 155px;
                transition: all 0.4s;
                position: absolute;
                border-radius: 10px;
                background-color: var(--light-color4);
            }
        }
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all .8s;
            border-radius: 10px;
        }
    }
    &-wraper{
        padding-top: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        @include max (1399px){
            display: block;
        }
        @include max (1199px){
            display: flex;
        }
        @include max (575px){
            display: block;
        }
    }
    &-author{
        h4{
            color: var(--white-color);
            margin-bottom: 5px;
        }
        p{
            font-size: 18px;
            line-height: 26px;
            letter-spacing: -0.001em;
            color: var(--white-color);
        }
        &.authore2{
            h4{
                color: var(--heading-color);
            }
            p{
                color: var(--heading-color);
                font-size: 18px;
                font-weight: 400;
                line-height: 26px;
                letter-spacing: -0.01em;
            }
        }
    }
    &-wrap2{
        transition: all 0.4s;
        margin-bottom: 24px;
        &:hover{
            .bmix-team-thumb2 img{
                transform: scale(1.1);
            }
            &.content11{
                box-shadow: 0px 4px 80px 0px #00000014;
            }
        }
    }
    &-thumb2{
        overflow: hidden;
        position: relative;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all .6s;
        }
    }
    &-contact2{
        background: var(--dark-bg);
        padding: 20px 50px 20px 20px;
        border-radius: 0 0 10px 10px;
        h5{
            color: var(--white-color);
            margin-bottom: 4px;
        }
        p{
            color: var(--white-color);
        }
        &.content10{
            background: var(--white-bg);
            border: 1px solid var(--white-color);
            border-radius: 10px;
            h5{
                color: var(--heding-color);
            }
            p{
                font-size: 18px;
                line-height: 26px;
                letter-spacing: -0.001em;
                color: var(--heading-color);
            }
        }
    }
    &-details{
        &-wrap{
            position: relative;
        }
        &-thumb{
            text-align: center;
        }
        &-content{
            margin-top: 30px;
            h3{
                margin-bottom: 10px;
            }
        }
        &-info-wrap{
            display: flex;
            position: relative;
            justify-content: space-between;
            @include max (1199px){
                display: block;
            }
            .border-r{
                position: absolute;
                top: 25px;
                left: 44%;
                width: 1px;
                height: 120px;
                border-right: 1px solid var(--light-color2);
                @include max (1199px){
                    display: none;
                }
                
            }
            
        }
        &-info-content{
            margin-top: 30px;
            &:first-child{
                @include max (767px){
                    margin-bottom: 30px;
                }
            }
            img{
                margin-bottom: 24px;
            }
            h3{
                margin-bottom: 8px;
            }
            a{
                color: var(--heading-color);
                
            }
            .team-details{
                margin-top: 30px;
            }
        }
        &-skill{
            h3{
                margin-bottom: 15px;
            }
            p{
                margin-bottom: 20px;
            }
        }
        &-experience{
            h3{
                margin-bottom: 15px;
            }
            p{
                margin-bottom: 20px;
            }
            &-info{
                display: flex;
                gap: 50px;
            }
            &-details{
                h5{
                    margin-bottom: 10px;
                }
                p{
                    font-size: 18px;
                    font-weight: 400;
                    line-height: 26px;
                    letter-spacing: -0.01em;
                }
            }
        }
    }
    &-v8{
        h2{
            color: var(--white-color);
        }
    }
}

.bmix-skill-title{
    h5{
        margin-bottom: 12px;
    }
}
  
  .item {
    width: 200px;
    height: 200px;
    margin: 50px auto;
    padding-top: 75px;
    background: #ccc;
    text-align: center;
    color: #FFF;
    font-size: 3em;
  }
  