.bmix-section-title {
    padding-bottom: 80px;
    @include max(991px) {
        padding-bottom: 60px;
        text-align: center;
    }
    @include max(767px) {
        padding-bottom: 40px;
    }
    &.center{
        max-width: 650px;
        margin: 0 auto;
        text-align: center;
        &.max-width-620 {
            max-width: 620px;
        }
        &.max-width-630 {
            max-width: 630px;
        }
        &.max-width-680 {
            max-width: 680px;
        }
        &.max-width-700 {
            max-width: 700px;
        }
        &.max-width-750 {
            max-width: 750px;
        }
        &.team-v9{
            h2{
                color: var(--white-color);
            }
        }
    }
}

