    }
}

.bmix-default-content {
    h2 {
        margin-bottom: 18px;
    }
    &.about-v7{
        padding-bottom: 50px;
        border-bottom: 1px solid rgba(30, 38, 61, 0.1);
        @include max (991px){
            padding-bottom: 40px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
    }
    &.content-v11{
        padding-bottom: 30px;
        border-bottom: 1px solid rgba(30, 38, 61, 0.2);
    }
    &.pr-10{
        padding-right: 10%!important;
        @include max (767px){
            padding-right: 0;
        }
    }
}

.mr-50 {
    margin-right: 50px;
    @include max (1399px){
        margin-right: 0;
    }
}


  






