.bmix-about{
    &-content{
        gap: 50px;
        display: flex;
        justify-content: space-between;
        @include max (1199px){
            gap: 20px;
        }
        @include max (767px){
            display: block;
        }
    }
    &-thumb{
        position: relative;
        text-align: center;
    }
    &-item{
        h4{
            margin-bottom: 10px;
        }
        p{
            font-size: 18px;
            line-height: 26px;
            letter-spacing: -0.001em;
        }
        @include max (767px){
            &:first-child{
                margin-bottom: 20px;
            }
        }
    }
    // end v1
    
    &-content-wrap{
        display: flex;
        align-items: center;
        border-radius: 10px;
        padding: 30px 40px;
        gap: 30px;
        border: 1px solid var(--light-color2);
        margin-bottom: 15px;
        @include max (1199px){
            padding: 20px;
            gap: 20px;
        }
        @include max (991px){
            padding: 30px 40px;
            gap: 30px;
        }
        @include max (767px){
            justify-content: center;
        }
    }
    &-content-icon{
        flex-shrink: 0;
    }
    //  end v2
    &-content-wrap2{
        display: flex;
        gap: 50px;
        @include max (767px){
            display: block;
        }
    }
    &-content-title{
        @include max (767px){
            margin-top: 40px;
        }
        ul {
            li{
                position: relative;
                padding-left: 50px;
                @include max (991px){
                    text-align: left;
                }
                h5{
                    margin-bottom: 10px;
                }
                h6{
                    margin-bottom: 10px;
                }
                img{
                    position: absolute;
                    top: 2px;
                    left: 0;
                }
                p{
                    font-size: 18px;
                    line-height: 26px;
                    letter-spacing: -0.001em;
                }
            }
        }
        &.about-v5{
            ul{
                li{
                    padding-left: 40px;
                    img{
                        width: 28px;
                    }
                }
            }
        }
        &.about-v7{
            margin-right: 70px;
            @include max (991px){
                margin-right: 0;
            }
            ul{
                li{
                    padding-left: 40px;
                    img{
                        width: 28px;
                    }
                    p{
                        font-size: 21px;
                        line-height: 30px;
                        letter-spacing: -0.001em;
                    }
                }
            }
        }
        &.about-v9{
            padding-right: 175px;
            @include max (991px){
                padding-right: 0;
            }
            ul{
                li{
                    padding-left: 40px;
                    margin-bottom: 24px;
                    &:last-child{
                        margin-bottom: 0;
                    }
                    img{
                        width: 28px;
                    }
                    p{
                        font-size: 21px;
                        line-height: 30px;
                        letter-spacing: -0.001em;
                    }
                }
            }
        }
        &.v9-content-item{
            margin-bottom: 35px;
            &:last-child {
                margin-bottom: 0;
            }
            ul{
                li{
                    padding-left: 70px!important;
                }
            }
            h5{
                color: var(--white-color);
            }
            p{
                font-size: 21px;
                line-height: 30px;
                letter-spacing: -.001em;
                color: var(--white-color);
            }
            &:hover{
                .bmix-process-number{
                    h5{
                        background: var(--accent-color);
                        border: 1px solid var(--accent-color);
                        color: var(--white-color)!important;
                    }
                }
            }
        }
    }
    // v10
    &-tab-btn{
        display: flex;
        justify-content: space-between;
        @include max (991px){
            gap: 30px!important;
        }
        @include max (767px){
            gap: 30px;
        }
        @include max (575px){
            display: block;
        }
    }
    // strat-inner-page

    &-title-thumb{
        img{
            border-radius: 10px;
        }
    }
    &-us-wrap{
        position: relative;
        @include max (991px){
            margin-bottom: 40px;
            &.mb-0{
                margin-bottom: 0;
            }
        }
        @include max (767px){
            margin-bottom: 30px;
        }
    }
    &-us-shape{
        margin-bottom: 50px;
        @include max (991px){
            margin-bottom: 40px;
        }
        @include max (767px){
            margin-bottom: 30px;
        }
    }
    &-us-date{
        margin-bottom: 24px;
        ul{
            li{
                font-size: 18px;
                font-weight: 700;
                line-height: 26px;
                letter-spacing: -0.01em;
                color: var(--accent-color);
            }
        }
    }
    &-us-border-bottom{
        position: relative;
        &::before{
            content: "";
            top: -74%;
            left: 0;
            width: 100%;
            height: 100%;
            position: absolute;
            border-bottom: 2px solid var(--light-color2);
            @include max (1399px){
                top: -77%;
            }
            @include max (1199px){
                display: none;
            }
        }
    }
    &-us-shape{
        @include max (1199px){
            display: none;
        }
    }
    &-us-title{
        h4{
            margin-bottom: 15px;
        }
    }

}



.ml-81{
    margin-left: 80px;
    @include max (1199px){
        margin-left: 50px;
    }
    @include max (991px){
        margin-left: 0px;
    }
}
.mr-70{
    margin-right: 70px;
    @include max (991px){
        margin-right: 0;
    }
}
.mr-40{
    margin-right: 40px;
    @include max (1199px){
        margin-right: 0;
    }
}

.ml-40{
    margin-left: 40px;
    @include max (991px){
        margin-left: 0;
    }
}

@keyframes updown {
	0% {
		transform: translatey(0px);
	}
	50% {
		transform: translatey(-70px);
	}
	100% {
		transform: translatey(0px);
	}
}

.bmix-content{
    &-wrap{
        margin-bottom: 24px;
        &:last-child{
            margin-bottom: 0;
        }
        h4{
            margin-bottom: 10px;
        }
    }
}
.about-vr7{
    @include max (991px){
        justify-content: center;
    }
    ul{
        li{
            img{
                width: 21px;
                height: 21px;
            }
        }
    }
}

.mr-52 {
    margin-right: 50px;
    @include max (991px){
        margin-right: 0;
    }
}