.bmix-counter{
    &-wrap{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        gap: 30px;
        text-align: center;
        @include max (767px){
            justify-content: center;
        }
    }
    &-data{
        font-size: 100px;
        line-height: 1.07em; 
        letter-spacing: -0.005em;
        font-weight: 700;
        color: var(--accent-color2);
        @include max(1199px) { 
            font-size: 60px;
            letter-spacing: -3px;
        }
        @include max(991px) { 
            font-size: 48px;
        }
        
        p{
            font-size: 24px;
            margin-top: 15px;
            line-height: 32px;
            letter-spacing: -0.005em;
            color: var(--white-color);
        }
    }
    &-data2{
        font-size: 100px;
        line-height: 1.07em; 
        letter-spacing: -0.005em;
        font-weight: 700;
        color: var(--white-color);
        @include max(1199px) { 
            font-size: 60px;
            letter-spacing: -3px;
        }
        @include max(991px) { 
            font-size: 48px;
        }
        
        p{
            font-size: 24px;
            line-height: 32px;
            letter-spacing: -0.005em;
            color: var(--white-color);
        }
    }

    &-data3{
        font-size: 65px;
        line-height: 1.07em; 
        letter-spacing: -0.005em;
        font-weight: 700;
        color: var(--heading-color);
        @include max(1199px) { 
            font-size: 60px;
            letter-spacing: -3px;
        }
        @include max(991px) { 
            font-size: 48px;
        }
        
        p{
            font-size: 24px;
            line-height: 32px;
            margin-top: 15px;
            letter-spacing: -0.005em;
            color: var(--heading-color)!important;
        }
    }
    // end
    &-wrapv4{
        display: flex;
        gap: 80px;
        @include max (575px){
            display: block;
            text-align: center;
        }
    }
    &-data4{
        font-size: 65px;
        line-height: 1.07em; 
        letter-spacing: -0.005em;
        font-weight: 700;
        color: var(--heading-color);
        @include max(1199px) { 
            font-size: 60px;
            letter-spacing: -3px;
        }
        @include max(991px) { 
            font-size: 48px;
            font-weight: 600;
        }
        
        p{
            font-size: 21px;
            line-height: 30px;
            font-weight: 400;
            margin-top: 5px;
            letter-spacing: -0.001em;
            color: var(--body-color)!important;
        }
    }
    // end
    &-wrapv6{
        display: flex;
        justify-content: space-between;
        @include max (575px){
            display: block;
            &:nth-child(2){
                margin: 24px 0;
            }
        }
    }
    &-data5{
        font-size: 65px;
        line-height: 1.07em; 
        letter-spacing: -0.005em;
        font-weight: 700;
        text-align: center;
        color: var(--accent-color);
        span{
            color: var(--heading-color);
        }
        @include max(1199px) { 
            font-size: 60px;
            letter-spacing: -3px;
        }
        @include max(991px) { 
            font-size: 48px;
            font-weight: 600;
        }
        p{
            font-size: 21px;
            line-height: 30px;
            font-weight: 400;
            margin-top: 5px;
            letter-spacing: -0.001em;
            color: var(--body-color)!important;
        }
        &.data-left{
            text-align: left;
            @include max (575px){
                text-align: center;
            }
        }
    }
    // end
    &-wrapv7{
        padding: 40px;
        text-align: center;
        border-radius: 10px;
    }
    &-data6{
        font-size: 65px;
        line-height: 1.07em; 
        letter-spacing: -0.005em;
        font-weight: 700;
        text-align: center;
        color: var(--white-color);
        padding-bottom: 30px;
        border-bottom: 1px solid var(--body-color);
        margin-bottom: 30px;
        &:last-child{
            padding-bottom: 0;
            border-bottom: none;
            margin-bottom: 0;
        }
        @include max(1199px) { 
            font-size: 60px;
            letter-spacing: -3px;
        }
        @include max(991px) { 
            font-size: 48px;
        }
        p{
            font-size: 21px;
            line-height: 30px;
            font-weight: 400;
            margin-top: 5px;
            letter-spacing: -0.001em;
        }
    }
}
.margin-bt{
    @include max (575px){
        margin: 20px 0;
    }
}

.border-r3 {
    position: absolute;
    top: 22%;
    left: 30%;
    display: block;
    height: 80px;
    border-right: 1px solid var(--body-color);
    @include max (1199px){
        display: none;
    }
}
.border-r4 {
    position: absolute;
    top: 22%;
    left: 72%;
    display: block;
    height: 80px;
    border-right: 1px solid var(--body-color);
    @include max (1199px){
        display: none;
    }
}

.border-r5 {
    position: absolute;
    top: 22%;
    left: 30%;
    display: block;
    height: 80px;
    border-right: 1px solid var(--light-color2);
    @include max (1199px){
        display: none;
    }
}
.border-r6 {
    position: absolute;
    top: 22%;
    left: 72%;
    display: block;
    height: 80px;
    border-right: 1px solid var(--light-color2);
    @include max (1199px){
        display: none;
    }
}

.border-r8 {
    position: absolute;
    top: 22%;
    left: 30%;
    display: block;
    height: 50px;
    opacity: .2;
    border-right: 1px solid var(--heading-color);
    @include max (767px){
        display: none;
    }
    &.border-color{
        border-right: 1px solid var(--light-color4);
        opacity: .5;
        left: 33%;
    }  
}
.border-r9 {
    position: absolute;
    top: 22%;
    left: 68%;
    display: block;
    height: 50px;
    opacity: .2;
    border-right: 1px solid var(--heading-color);
    @include max (767px){
        display: none;
    }
    &.border-color{
        border-right: 1px solid var(--light-color4);
        opacity: .5;
        top: 22%;
        left: 70%;
    }
}




.bmix-cta-counter{
    &-wrap{
        @include max (991px){
            text-align: center;
            margin-bottom: 40px;
        }
    }
    &-data{
        font-size: 100px;
        line-height: 96px;
        font-weight: 700;
        letter-spacing: -0.005em;
        color: var(--white-color);
        h4{
            font-size: 30px!important;
            line-height: 40px;
            color: var(--white-color);
            margin: 24px 0 10px 0;

        }
        p{
            font-size: 21px;
            line-height: 28px;
            font-weight: 400;
            letter-spacing: -0.001em;
            color: var(--white-color);
        }
    }
}